import React, { useRef, useState } from 'react';
import ClearAllicon from '@material-ui/icons/ClearAll';
import img1 from '../Images/image.jpg';
import SchoolIcon from '@material-ui/icons/School';
import WorkHistoryIcon from '@material-ui/icons/History';
import Computer from '@material-ui/icons/Computer';
import InsertChartIcon from '@material-ui/icons/InsertChart';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import Education from './Education';
import {BrowserRouter, Link} from "react-router-dom";
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import emailjs from "emailjs-com";
import GoToTop from './GoToTop';




const ProgrammingSkills =()=> {
  const [show,setShow] = useState(false);
  const ref =useRef(null);
  const handleClick =()=>{
       ref.current?.scrollIntoView({behavior:'smooth'})
  }
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_01io2jg', 'template_7i8b81m', form.current, 'XnqrgPoLE4jOlEUqe')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset()
  };
    
    
        return (
            <div className='container-fluid' >
                <div className='row' id='id1'>
                    <div className='col' id='navid'>
                    <nav class="navbar navbar-expand-lg bg-body-tertiary" >
  <div class="container-fluid">
    <a className="navbar-brand" href="#">Lakshay</a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarNav">
      <ul class="navbar-nav">
        <li class="nav-item">
        <Link style={{color: 'Gray',textDecoration:"none"}} to="/Portfolio"><a class="nav-link active" aria-current="page" > Home</a></Link>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#">AboutMe</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#">Resume</a>
        </li>
        <li class="nav-item">
          <a class="nav-link ">ContactMe</a>
        </li>
      </ul>
    </div>
  </div>
</nav>
                    </div>
                </div>





                <div className='row ' id='id2'>
                <div className='col' id='col1'>
                      <h2> Hello,I'm Lakshay</h2>
                      <h3>Knack of Building Applications with front and back end operations</h3>
                      <div className='btn' onClick={handleClick} id='btnid1' >Hire Me</div>

                     <a href="https://drive.google.com/file/d/17plOp9ZHYc1AT3RQqgr-H0FqHZdk_DNS/view?usp=sharing"><div className='btn' id='btnid2' > Get Resume</div></a>
                </div>
                <div className='col' id='col2'>
                <div id='img'><img src={img1} /></div>
                </div>
            </div>





            
            



            <div className='row' id='id4'>
                  <div className='col'>
                    <h1>Resume</h1>
                    <p1>My Formal Bio Details</p1>
                    </div> 
                    </div>

                    <div className='row' id='id6'> 
                <div className='col' id='waste'>
                        <div className='box' id='boxid2'>
                            <p1><SchoolIcon/></p1>
                            <p1><WorkHistoryIcon/></p1>
                            <p1><Computer/></p1>
                            <p1><InsertChartIcon/></p1>
                            <p1><PlayCircleFilledIcon/></p1>
                        </div>
                    <div className='box' id='boxid'>
                        <p> <Link style={{color: 'white',textDecoration:"none"}} to="/Education"> Educations</Link></p>
                         <p><Link style={{color: 'white',textDecoration:"none"}} to="/WorkHistory"> Work History</Link></p>
                         <p><Link style={{color: 'white',textDecoration:"none"}} to="/Programming"> Programming Skills</Link></p>
                         <p><Link style={{color: 'white',textDecoration:"none"}} to="/Project"> Projects</Link></p>
                         <p><Link style={{color: 'white',textDecoration:"none"}} to="/Interest"> Interests</Link></p> 
                          
                    </div>
                </div>

                <div className='col' id='skill'> 
                <div className='card' id='card1'>Html</div>
                <div className='card' id='card2'>css</div>
                <div className='card'id='card3'>javaScript</div>
                <div className='card'id='card4'>React js</div>
                <div className='card'id='card4'>Firebase
                </div>
                 </div>
            </div>

            <div className='row' ref={ref} id='id5'>
  <div className='col'><h1>Contact</h1></div>
    <div className='col'> 
     <form ref={form} onSubmit={sendEmail} id="form">
      <div className='row pt-5 mx-auto'>
        <div className='col-8 form-group mx-auto'>
          <input type="text" className="form-control" placeholder="Name" name="name"  />
        </div>
        <div className='col-8 form-group mx-auto'>
          <input type="email" className="form-control" placeholder="Email-Address" name="email"  />
        </div>
        <div className='col-8 form-group mx-auto'>
          <input type="text" className="form-control" placeholder="Subject" name="subject"  />
        </div>
        <div className='col-8 form-group mx-auto'>
          <textarea className='form-control' id='' cols="30" rows="8" placeholder="Your Message" name="message" ></textarea>
        </div>
        <div className='col-8 pt-3 mx-auto'>
          <input type="submit" className="btn btn-info" value="Send Message" ></input>

        </div>

      </div>
     </form>

    </div>

</div>
<GoToTop />

<div className='row' id='bottomnavbarrow'>
<div className='col'id='bottomnavbarcol'>
  {show && 
<div class="card" id='lowercard' >
  <div class="card-body">  
    <div className='row' id='cardrow'>
      <div className='col' id='cardcol'>
      <button className='btn btn-primary'>Home</button>
      <button className='btn btn-primary'>About Me</button>
      </div>
       </div>
    <div className='row' id='cardrow1'>
    <div className='col'>
    <button className='btn btn-primary'>Resume</button>
    <button className='btn btn-primary'>ContactMe</button>
          </div>
       </div>
  </div>
</div>
}
<div className='icn' onClick={()=>setShow(!show)} >{show === true ? <ClearAllicon />:<ClearAllicon />}</div>
</div>
</div>
            </div>
        )
    }


export default ProgrammingSkills;